import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { UnreliableVendorHistory } from '@biz-nest/commerce-client';

import {
  AppNameService,
  EventbusService, ListComposeAdminPanel,
  MainBarTitleService,
  scrollToTop, WindowService
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';

export abstract class UnReliableVendorHistoryListBase {
  posting = false;
  cleanup?: () => void;
  listCompose = new ListComposeAdminPanel<UnreliableVendorHistory>(
    UnreliableVendorHistory,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  actions = [
    {
      label: 'View',
      icon: 'visibility',
      event: (unreliableVendorHistory: UnreliableVendorHistory) => this.view(unreliableVendorHistory)
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (unreliableVendorHistory: UnreliableVendorHistory) => this.remove(unreliableVendorHistory)
    }
  ];

  constructor(
    protected windowService: WindowService,
    protected toastr: ToastrService,
    protected router: Router,
    protected eventbus: EventbusService,
    protected apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    protected metaTagService: Meta,
    protected titleService: Title
  ) { }

  init(): void {
    this.listCompose.getDataArr();
    this.mainBarTitle.title = 'Unreliable Vendors History';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `Efficiently manage and comprehensively monitor your unreliable vendors,
      empowering you to maintain financial clarity and make informed decisions
      regarding your fiscal responsibilities and opportunities for savings.`
    });

    scrollToTop(this.windowService);
  }

  abstract view(hist: UnreliableVendorHistory): void;

  abstract remove(hist: UnreliableVendorHistory): void;

  destroy(): void {
    this.listCompose.cleanup?.();
  }
}
